import React, { Fragment } from 'react';

const Navbar = () => {
  const languageList = (
    <Fragment>
      <li>
        <a href="#!" className="center-align">
          العربية
        </a>
      </li>
      <li className="divider"></li>
      <li>
        <a href="#!" className="center-align">
          English
        </a>
      </li>
    </Fragment>
  );
  return (
    <Fragment>
      <div className="navbar-fixed">
        <nav className="deep-orange darken-1">
          <div className="nav-wrapper">
            <a href="#!" className="brand-logo right">
              دروس Logo
            </a>
            <a href="#!" data-target="mobile-demo" className="sidenav-trigger">
              <i className="material-icons">menu</i>
            </a>

            <ul className="left hide-on-med-and-down">
              <li>
                <a
                  className="dropdown-trigger"
                  href="#!"
                  data-target="dropdown1"
                >
                  <i className="material-icons right">arrow_drop_down</i>
                  اختيار اللغة
                </a>
              </li>
              <li>
                <a href="sass.html">اتصل بنا</a>
              </li>
              <li>
                <a href="badges.html">الأخبار</a>
              </li>
              <li className="active">
                <a href="collapsible.html">الرئيسية</a>
              </li>
            </ul>
          </div>
        </nav>
      </div>

      {/* Dropdown Structure Start*/}
      {/* 1 */}
      <ul id="dropdown1" className="dropdown-content">
        {languageList}
      </ul>
      {/* 2 */}
      <ul id="dropdown2" className="dropdown-content">
        {languageList}
      </ul>
      {/* Dropdown Structure End*/}

      <ul className="sidenav" id="mobile-demo">
        <li className="active right-align">
          <a href="collapsible.html">الرئيسية</a>
        </li>
        <li className="right-align">
          <a href="http://google.com/">الأخبار</a>
        </li>
        <li className="right-align">
          <a href="sass.html">اتصل بنا</a>
        </li>
        <li className="active right-align">
          <a className="dropdown-trigger" href="#!" data-target="dropdown2">
            <i className="material-icons right">arrow_drop_down</i>
            اختيار اللغة
          </a>
        </li>
      </ul>
    </Fragment>
  );
};

export default Navbar;
